import GoogleLogin from  "@stack-pulse/next-google-login"

import Cookies from "js-cookie";
import jwt from 'jsonwebtoken';

const credentials = {
  "google":process.env.REACT_APP_GOOGLE_CLIENTID,
  "jwt":process.env.REACT_APP_JWT
}
function Login() {
    function onLogin(googleResponse){
        console.log(googleResponse);
        const token = jwt.sign({email:googleResponse.profileObj.email}, credentials.jwt)
        Cookies.set("access_token",token, { expires: 15/86400 });
    }

    function saveCookie(){
      const token = jwt.sign({email:"teste"}, credentials.jwt)
      Cookies.set("access_token",token, { expires: 15/86400 });
    }
  return (
    <div className="GoogleLogin">
      <button onClick={saveCookie}>TESTE</button>
        <GoogleLogin
        clientId={credentials.google}
        buttonText="Continue with Google"
        onSuccess={onLogin}
        onFailure={console.log}
        cookiePolicy={'single_host_origin'}
        />
        
    </div>
  );
}

export default Login;
